.bred{
    padding: 10px 16px;
    list-style: none;
    li{
        display: inline;
        font-size: 18px;
    }
    li+li::before{
        padding: 8px;
        color: black;
        content: "/\00a0";
    }
    a{
        cursor: pointer;
        text-transform: uppercase;
        color: red;
        &:hover{
            color: #01447e;
            text-decoration: underline;
        }

    }
}